var n_stage = 5;
var n_color = 6;

var yellow = [[232, 231, 159], [120, 119, 36]];
var green = [[157, 231, 157], [32, 119, 34]];
var cyan = [[159, 232, 229], [33, 120, 127]];
var pupple = [[155, 157, 230], [28, 31, 118]];
var magenta = [[231, 157, 230], [119, 32, 118]];
var red = [[230, 155, 156], [119, 28, 30]];

function devideInterval(interval_color, v0, v1, n) {
    var intervals = [];
    var begin = interval_color[0];
    var end = interval_color[1];

    function linear(x0, x1, i, _n) {
        return x0 + (x1 - x0) / _n * i;
    }

    intervals.push([linear(v0, v1, 1, n), begin]);

    for (var i = 1; i < n - 1; i++) {
        var v = linear(v0, v1, i + 1, n);
        var color = [
            linear(end[0], begin[0], i, n - 1),
            linear(end[1], begin[1], i, n - 1),
            linear(end[2], begin[2], i, n - 1),
        ];
        intervals.push([v, color]);
    }
    intervals.push([v1, end]);
    return intervals;
}

function buildWindToColor() {
    var winds = [
        [1, yellow],
        [3.5, green],
        [6, cyan],
        [10, pupple],
        [35, magenta],
        [60, red]
    ];

    var intervals = [];
    var v0 = 0;
    for (var i = 0; i < winds.length; i++) {
        intervals = intervals.concat(devideInterval(winds[i][1], v0, winds[i][0], n_stage));
        v0 = winds[i][0];
    }

    return function (windSpeed) {
        for (var i = 0; i < intervals.length; i++) {
            if (windSpeed < intervals[i][0])
                return intervals[i][1];
        }

        return intervals[intervals.length - 1][1];
    };
}

var windToRGB = buildWindToColor();

function asColorStyle(rgb, a) {
    return "rgba(" + rgb[0] + ", " + rgb[1] + ", " + rgb[2] + ", " + a + ")";
}

function drawColorScale() {
    var canvas = document.getElementById("scaleCanvas");
    var ctx = canvas.getContext('2d');

    var colors = windColors();
    var w = canvas.width / colors.length;
    var x = 0;
    for (var i = 0; i < colors.length; i++) {
        ctx.fillStyle = asColorStyle(colors[i]);
        ctx.fillRect(x, 0, w, canvas.height);
        x += w;
    }
}


/**
 * Produces a color style in a rainbow-like trefoil color space. Not quite HSV, but produces a nice
 * spectrum. See http://krazydad.com/tutorials/makecolors.php.
 *
 * @param hue the hue rotation in the range [0, 1]
 * @param a the alpha value in the range [0, 255]
 * @returns {Array} [r, g, b, a]
 */
const τ = 2 * Math.PI;

function sinebowColor(hue) {
    // Map hue [0, 1] to radians [0, 5/6τ]. Don't allow a full rotation because that keeps hue == 0 and
    // hue == 1 from mapping to the same color.
    var rad = hue * τ * 5 / 6;
    rad *= 0.75;  // increase frequency to 2/3 cycle per rad

    var s = Math.sin(rad);
    var c = Math.cos(rad);
    var r = Math.floor(Math.max(0, -c) * 255);
    var g = Math.floor(Math.max(s, 0) * 255);
    var b = Math.floor(Math.max(c, 0, -s) * 255);
    return [r, g, b];
}

function colorInterpolator(start, end) {
    var r = start[0], g = start[1], b = start[2];
    var Δr = end[0] - r, Δg = end[1] - g, Δb = end[2] - b;
    return function (i, a) {
        return [Math.floor(r + i * Δr), Math.floor(g + i * Δg), Math.floor(b + i * Δb), a];
    };
}

var BOUNDARY = 0.45;
var fadeToWhite = colorInterpolator(sinebowColor(1.0, 0), [255, 255, 255]);

/**
 * Interpolates a sinebow color where 0 <= i <= j, then fades to white where j < i <= 1.
 *
 * @param i number in the range [0, 1]
 * @param a alpha value in range [0, 255]
 * @returns {Array} [r, g, b, a]
 */
export function extendedSinebowColor(i) {
    return i <= BOUNDARY ?
        sinebowColor(i / BOUNDARY) :
        fadeToWhite((i - BOUNDARY) / (1 - BOUNDARY));
}

