function getColor(v, color_table) {
    function interpolate(a, s, e) {
        function from_range(v0, v1) {
            // return a >= 0.5 ? v1 : v0;
            return Math.round((v1 - v0) * a + v0);
        }

        return [
            from_range(s[0], e[0]),
            from_range(s[1], e[1]),
            from_range(s[2], e[2])
        ];
    }

    for (var i = 1; i < color_table.length; i++) {
        if (v < color_table[i][0]) {
            var v0 = color_table[i - 1][0];
            var v1 = color_table[i][0];

            var a = (v - v0) / (v1 - v0);

            var rgb = interpolate(a, color_table[i - 1][1], color_table[i][1]);
            return rgb;
        }
    }

    return color_table[color_table.length - 1][1];
}

export function windToColor(v) {
    return getColor(v, WIND_COLOR_TABLE);
}

export function windVectorToColor(v) {
    var windSpeed = Math.sqrt(v[0] * v[0] + v[1] * v[1]);
    return windToColor(windSpeed);
}

export function tempToColor(v) {
    return getColor(v, TEMP_COLOR_TABLE);
}

export function rainToColor(v) {
    return getColor(v, RAIN_COLOR_TABLE);
}

export function humidityToColor(v) {
    return getColor(v, HUMIDITY_COLOR_TABLE);
}

