<template>
    <div id="container">
        <div id="map"></div>
        <div id="controls">
            <SideMenu v-model="overlay_type" :is_mobile="is_mobile"></SideMenu>
            <mobile-time-slider
                    v-if="is_mobile"
                    @scrollend="onSliderChange"
                    class="mobile-time-slider"
                    v-slot="props"
            >
                {{ mobile_get_time(props.value)}}
            </mobile-time-slider>
            <v-custom-slider v-else
                             class="slider"
                             v-model="slider_pos"
                             thumb-label="always"
                             thumbColor="orange"
                             trackColor="black"
                             trackFillColor="orange"
                             @change="onSliderChange"
            >
                <template v-slot:thumb-label="props">
                    {{ get_time }}
                </template>
                <template v-slot:cursor-box-content="props">
                    <div class="gray-box">
                        {{convertToTime(now, props.value)}}
                    </div>
                    <div class="reverse-triangle"></div>
                </template>
                <template v-slot:append>
                    <v-btn fab dark x-small depressed color="orange">
                        <v-icon
                                @click="refreshAwsData"
                        >
                            mdi-refresh
                        </v-icon>

                    </v-btn>
                </template>
            </v-custom-slider>
            <div class="timestamp_box" :style="is_mobile?'left: 10px':'right: 10px'">
                {{get_date_time}}
            </div>
            <scale-bar :is_mobile="is_mobile" :overlay_type="overlay_type"></scale-bar>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';
    import 'leaflet/dist/leaflet.css';
    import L from 'leaflet';
    import SideMenu from "@/components/SideMenu";
    import {addIntensityLayer} from "@/intensity_layer";
    import {calcDate, convertToTime, dateToStr, padZero, parseStrToDate} from "@/logic";
    import axios from 'axios'
    import {interpolate_to_grid} from "@/interpolate";
    import {make_station_grid} from "@/station_grid";
    import {addDebugCoordsLayer} from "@/debug_coords_layer";
    import {addStationLayer} from "@/station_layer";
    import {addAnimationLayer} from "@/animation_layer";
    import {WIND} from "@/value";
    import VCustomSlider from "@/VCustomSlider";
    import MobileTimeSlider from "@/components/MobileTimeSlider";
    import PopupBox from "@/components/PopupBox";
    import {custom_popup} from "@/custom_popup"
    import ScaleBar from "@/components/ScaleBar";

    var RES = 10000;
    var MAX_SEARCH_RADIUS = 40000;
    var MAX_NEIGHBORS = 10;

    function convertProjection(aws_data) {
        for (let feature of aws_data.features) {
            const lnglat = feature.geometry.coordinates;
            const coord = L.CRS.EPSG3857.project(L.latLng(lnglat[1], lnglat[0]));
            feature.geometry.coordinates = [coord.x, coord.y];
            feature.properties.rain_15m = feature.properties.rain_15m * 4;
        }

        return aws_data;
    }

    export default {
        name: "Map",
        components: {ScaleBar, SideMenu, VCustomSlider, MobileTimeSlider},
        props: ["params", "is_mobile"],

        mounted() {
            this.initMap();
        },

        data: function () {
            return {
                map: null,
                overlay_type: this.params.t,
                timestamp: null,
                query_pos: 100,
                grids: {},
                slider_pos: 100,
                aws_data: null,
                station_layer: null,
                animation_layer: null,
                coastline_layer: null,
                intensity_layer: null,
                label_layer: null,
                popup: {
                    show: false,
                    value: null,
                    overlay: null,
                },
                station_popup: {
                    show: false,
                    value: null,
                    overlay: null,
                },
                v: null,
                now: new Date(),
                query_dt: null,
                cursor_x_on_slider: 0,
                debug_msg: "",
                popup: null,
            }
        },
        methods: {
            initMap() {
                var lng = this.params.lo;
                var lat = this.params.la;
                var z = this.params.z;

                if (lng == null)
                    lng = this.is_mobile ? 127.69 : 128.59;
                if (lat == null)
                    lat = this.is_mobile ? 36.95 : 36.155;
                if (z == null)
                    z = this.is_mobile ? 6 : 7;

                const latlng = L.latLng(lat, lng);

                const maxBounds = L.latLngBounds(
                    L.latLng(30.5, 122.5),
                    L.latLng(40, 134));

                this.map = L.map('map',
                    {
                        center: latlng,
                        zoom: z,
                        minZoom: 6,
                        maxZoom: 15,
                        attributionControl: true,
                        zoomControl: false,
                        maxBounds
                    }
                );

                // console.log(this.map.project(latlng));
                // console.log(L.CRS.EPSG3857.project(latlng));
                // console.log(this.map.latLngToLayerPoint(latlng));
                // console.log(this.map.getPixelOrigin());
                // console.log(this.map.getPixelBounds());

                this.intensity_layer = addIntensityLayer(this.map, {zIndex: 0});

                var osmTileLayer = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                    attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
                    zIndex: 1
                });
                var windyTileLayer = L.tileLayer(process.env.VUE_APP_TILE_API_URL, {
                    attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
                    zIndex: 1
                });

                windyTileLayer.addTo(this.map);

                this.station_layer = addStationLayer(this.map, {
                    overlay_type: this.overlay_type,
                    zIndex: 2,
                    is_mobile: this.is_mobile
                });
                this.animation_layer = addAnimationLayer(this.map, {zIndex: 3});

                this.map.on('moveend', () => {
                    this.station_layer && this.station_layer.redraw();
                    var center = this.map.getCenter();
                    this.params.lo = center.lng.toFixed(3);
                    this.params.la = center.lat.toFixed(3);
                    this.params.z = this.map.getZoom();
                    this.updateUrlHash();

                    // console.log(this.map.getBounds());
                });

                this.map.on('zoomend', () => {
                    if (this.map.getZoom() > 11.2) {
                        this.intensity_layer && this.intensity_layer.hide();

                        this.map.removeLayer(windyTileLayer);
                        if (!this.map.hasLayer(osmTileLayer))
                            osmTileLayer.addTo(this.map);
                        this.animation_layer.setParticleColor('red');
                    } else {
                        this.intensity_layer && this.intensity_layer.show();
                        this.map.removeLayer(osmTileLayer);
                        if (!this.map.hasLayer(windyTileLayer))
                            windyTileLayer.addTo(this.map);
                        this.animation_layer.setParticleColor('white');
                    }
                });

                this.popup = custom_popup();

                this.map.on('click', (e) => {
                    if (this.aws_data) {
                        var coord = L.CRS.EPSG3857.project(e.latlng);
                        // console.log('Clicked : ' + e.latlng);
                        coord = [coord.x, coord.y];
                        var value = this.getGrid(this.overlay_type).getInterpolatedValueStr(coord);
                        if (value) {
                            var popupBox = this.createPopupBox(value, this.popup._onCloseButtonClick.bind(this.popup));
                            this.popup
                                .setLatLng(e.latlng)
                                .setContent(popupBox)
                                .openOn(this.map);
                        }
                    }
                });

                this.retrieveAwsData(this.params.query_dt);
            },
            createPopupBox(value, closePopup) {
                var popupBox = new (Vue.extend(PopupBox))({propsData: {value}})
                popupBox.$mount();
                popupBox.$on('close-popup',
                    (e) => {
                        closePopup(e);
                    }
                );
                return popupBox.$el;
            }
            ,
            retrieveAwsData(query_dt, query_pos) {
                this.query_dt = query_dt;
                var reqUrl = process.env.VUE_APP_AWS_DATA_URL;
                if (query_dt)
                    reqUrl += '?dt=' + query_dt;
                axios.get(reqUrl).then((result) => this.onAwsDataChanged(result, query_pos));
            }
            ,
            onAwsDataChanged(result, query_pos) {
                if (result.data.error) {
                    alert(result.data.errmsg);
                    return;
                }

                this.query_pos = query_pos ? query_pos : 100;
                this.aws_data = convertProjection(result.data);
                this.now = new Date(result.headers.date);
                this.timestamp = parseStrToDate(this.aws_data.timestamp);
                if (this.timestamp > this.now)
                    this.now = this.timestamp;

                this.grids = {};

                this.station_grid = make_station_grid(this.aws_data, RES);
                this.intensity_layer && this.intensity_layer.changeSourceGrid(this.getGrid(this.overlay_type));
                this.station_layer && this.station_layer.setStationGrid(this.station_grid);
                this.animation_layer && this.animation_layer.setWindGrid(this.station_grid, this.getGrid(WIND));

                // if (this.animation_layer != null) {
                //     this.animation_layer.setWindGrid(this.station_grid, this.getGrid(WIND));
                //     this.animation_layer.start();
                // }
            }
            ,
            getGrid: function (overlay_type) {
                let grid = this.grids[overlay_type];
                if (grid === undefined) {
                    grid = interpolate_to_grid(this.station_grid, MAX_SEARCH_RADIUS, MAX_NEIGHBORS, overlay_type);
                    this.grids[overlay_type] = grid;
                }

                return grid;
            }
            ,
            updateUrlHash: function () {
                var params = this.params;
                window.location.hash = Object.keys(params)
                    .filter((k) => params[k] !== null)
                    .map(function (k) {
                        return k + '=' + params[k];
                    }).join('&');
            }
            ,
            onSliderChange: function (v) {
                // console.log("slider value changed: " + v);
                let query_dt = null;
                if (v !== 100) {
                    const dt = calcDate(this.now, v);
                    query_dt = dt.getFullYear()
                        + padZero(dt.getMonth() + 1)
                        + padZero(dt.getDate())
                        + padZero(dt.getHours())
                        + padZero(dt.getMinutes());
                }

                // console.log("query_dt: " + query_dt);
                if (query_dt == null || query_dt !== this.query_dt) {
                    this.retrieveAwsData(query_dt, v);
                }
            }
            ,
            mobile_get_time: function (slider_pos) {
                if (this.timestamp == null)
                    return dateToStr(new Date(), new Date());

                if (this.query_pos === slider_pos)
                    return dateToStr(this.now, this.timestamp);
                else
                    return convertToTime(this.now, slider_pos);
            }
            ,
            refreshAwsData: function () {
                this.slider_pos = 100;
                this.retrieveAwsData(null);
            }
            ,

        },
        computed: {
            get_date_time: function () {
                const t = this.timestamp;
                if (t == null)
                    return '';
                return t.getFullYear() + '년 '
                    + (t.getMonth() + 1)
                    + '월 '
                    + t.getDate()
                    + '일 '
                    + padZero(t.getHours())
                    + ':' + padZero(t.getMinutes());
            },
            get_time: function () {
                if (this.timestamp == null)
                    return dateToStr(new Date(), new Date());

                return dateToStr(this.now, this.timestamp);
            }
            ,
        }
        ,
        watch: {
            overlay_type: function (new_val, old_val) {
                this.station_layer && this.station_layer.setOverlayType(new_val);

                if (this.aws_data != null) {
                    this.intensity_layer != null && this.intensity_layer.changeSourceGrid(this.getGrid(new_val));
                }
                // console.log("Overlay type changed: " + new_val);
                this.params.t = new_val;
                this.updateUrlHash();
            }
            ,
        }

    }
</script>

<style>
    #container, #map {
        height: 100%;
        width: 100vw;
        position: fixed;
        left: 0;
        top: 0;
    }

    #controls {
        z-index: 2000;
        position: relative;
    }

    .timestamp_box {
        position: fixed;
        top: 10px;
        background: rgba(63, 61, 80, 0.76);
        color: white;
        border-radius: 15px;
        padding: 0 10px;
        z-index: 2000;
    }

    .debug_box {
        position: fixed;
        top: 10px;
        left: 10px;
        background: rgba(63, 61, 80, 0.76);
        color: white;
        padding: 0 10px;
    }

    .buttons {
        position: absolute;
        bottom: 20px;
        right: 5px;
    }

    .slider {
        position: absolute;
        bottom: 0;
        left: 20px;
        width: 80%;
    }

    /*썸 라벨 텍스트 설정*/
    .slider .v-slider--horizontal .v-slider__thumb-label > * {
        -webkit-transform: none;
        transform: none;
        padding: 0 0.8em;
        font-size: 1em;
    }

    /*썸 박스 설정*/
    .slider .v-slider--horizontal .v-slider__thumb-label {
        width: auto;
        height: auto;
        box-shadow: 0 0 4px 0 black;
        left: -25px;
        position: absolute;
        margin-bottom: 4px;
    }

    /*트랙 설정*/
    .slider .v-slider--horizontal {
        cursor: pointer;
    }

    .slider .v-slider--horizontal .v-slider__track-container {
        height: 7px;
    }

    /* thumb 아래 역삼각형 */
    .v-application .slider .v-custom-slider__thumb {
        width: 0;
        height: 0;
        position: absolute;
        left: -10px;
        bottom: 3px;
        z-index: 1;
        border-color: orange transparent transparent transparent !important;
        border-left: 8px solid;
        border-right: 8px solid;
        border-top: 14px solid;
        background-color: transparent !important;
    }


    /*마우스를 따라다니는 시간 박스*/
    .gray-box {
        position: absolute;
        width: auto;
        height: 18px;
        color: white;
        left: -15px;
        background: rgba(68, 65, 65, 0.84);
        border-radius: 0.5em;
        white-space: nowrap;
        font-size: 12px;
        padding: 0 0.8em 4px;
    }

    .reverse-triangle {
        width: 0;
        height: 0;
        position: absolute;
        bottom: -30px;
        border-color: rgba(68, 65, 65, 0.84) transparent transparent transparent !important;
        border-left: 8px solid;
        border-right: 8px solid;
        border-top: 13px solid;
        background-color: transparent !important;
    }

    .mobile-time-slider {
        position: fixed;
        bottom: 0px;
        width: 100vw;
        white-space: nowrap;
    }

    .leaflet-tooltip.station-label {
        background-color: transparent;
        border: transparent;
        box-shadow: none;
    }

    .leaflet-marker-icon.red-dot {
        background-color: red;
        border-radius: 50%;
    }

    .station-label {
        text-align: center;
        margin-left: -5px;
        margin-top: 5px;
        font-size: 14px;
    }

    .mobile-station-label {
        text-align: center;
        margin-left: -9px;
        margin-top: 5px;
        font-size: 18px;
    }

</style>