<template>
    <div :class="stylePrefix + 'bar_pos'">
        <div class="header">{{metadata.unit}}</div>
        <div class="bar" :style="barStyle">
            <span class="cell" :style="cellStyle" v-for="(entry, index) in scales" :key="index">{{entry}}</span>
        </div>
    </div>
</template>

<script>
    import {HUMIDITY, RAIN, TEMP, WIND} from "@/value";
    import {
        humidityToColor,
        rainToColor,
        tempToColor,
        windToColor
    } from "@/color_scale";

    export default {
        name: "ScaleBar",
        props: ['is_mobile', 'overlay_type'],
        data: function() {
            return {
                scalesPerType: {
                    [WIND]:     [  0,   3, 5,  7, 10, 15, 20, 30].reverse(),
                    [TEMP]:     [-25, -10, 0, 10, 18, 25, 35, 40].reverse(),
                    [RAIN]:     [  0,   1, 5, 10, 20, 50, 90, 150].reverse(),
                    [HUMIDITY]: [ 30,  40,50, 60, 70, 80, 90, 100].reverse()
                }

            }
        },
        methods: {
            toRGBStr(rgba) {
                return 'rgb(' + rgba[0] + ',' + rgba[1] + ',' + rgba[2] + ')';
            }
        },
        computed: {
            scales() {
                return this.scalesPerType[this.overlay_type];
            },
            cellStyle() {
                return {'height': 100 / (this.scales.length) + '%'};
            },

            barStyle() {
                var colors = this.scales.map((v) => {
                    return this.toRGBStr(this.metadata.getColor(v));
                });

                return {
                    'background': 'linear-gradient(' + colors.join(',') + ')'
                }
            },

            stylePrefix() {
                return this.is_mobile ? 'mobile_' : '';
            },

            metadata() {
                var getColor;
                var unit;
                switch (this.overlay_type) {
                    case WIND:
                        getColor = windToColor;
                        unit = 'm/s';
                        break;
                    case TEMP:
                        getColor = tempToColor;
                        unit =  '°C';
                        break;
                    case HUMIDITY:
                        getColor = humidityToColor;
                        unit = '%';
                        break;
                    case RAIN:
                        getColor = rainToColor;
                        unit = 'mm/h';
                        break;
                }

                return {getColor, unit};
            }
        }
    }
</script>

<style scoped>
    .header{
        font-size: 12px;
        color: black;
        /*background: orange;*/
        border-radius: 5px;
        text-shadow: black 0.1em 0.1em 0.2em;
    }
    .cell {
        display: block;
        text-align: center;
        font-size: 12px;
        color: white;
    }
    .bar {
        border-radius: 5px;
        width: 20px;
        height: 200px;
        box-shadow: 0 0 4px 0 black;
    }

    .bar_pos {
        position: fixed;
        right: 20px;
        bottom: 80px;
        width: 40px;
        alignment: center;
    }

    .mobile_bar_pos {
        position: fixed;
        left: 10px;
        bottom: 40px;
        width: 40px;
        alignment: center;
    }


</style>