import L from 'leaflet'
import {disableClickPropagation, disableScrollPropagation, on, stopPropagation} from "leaflet/src/dom/DomEvent";

L.Popup.Custom = L.Popup.extend({
    _initLayout: function () {
        var prefix = 'leaflet-popup-custom',
            container = this._container = L.DomUtil.create('div',
                prefix + ' ' + (this.options.className || '') +
                ' leaflet-zoom-animated');

        // var wrapper = this._wrapper = L.DomUtil.create('div', '', container);
        this._contentNode = L.DomUtil.create('div', prefix + '-content', container);
        var wrapper = this._contentNode;

        disableClickPropagation(wrapper);
        disableScrollPropagation(this._contentNode);
        on(wrapper, 'contextmenu', stopPropagation);

        if (this.options.closeButton) {
            var closeButton = this._closeButton = L.DomUtil.create('a', prefix + '-close-button', container);
            closeButton.href = '#close';
            closeButton.innerHTML = '&#215;';

            on(closeButton, 'click', this._onCloseButtonClick, this);
        }
    }
});

export function custom_popup(opt) {
    return new L.Popup.Custom(opt);
}
