import L from 'leaflet';
import {getTileBounds} from "@/globalmaptiles";

export function addIntensityLayer(map, opt) {
    var grid = null;
    var pendingTiles = [];

    L.GridLayer.IntensityLayer = L.GridLayer.extend({
        createTile: function (coords, done) {
            var tileSize = this.getTileSize();

            var tile = document.createElement('canvas');
            tile.setAttribute('width', tileSize.x);
            tile.setAttribute('height', tileSize.y);
            tile.setAttribute('name', coords.x+':'+coords.y+':'+coords.z)

            var tileBounds = getTileBounds(coords.x, coords.y, coords.z);

            if(grid == null)
                pendingTiles.push({tile, tileSize, tileBounds, done});
            else
                requestAnimationFrame(() => fillTile(tile, tileSize, tileBounds, 1, grid, done));

            // fillTile(tile, tileSize, tileBounds, 1, grid);
            return tile;
        }
    });

    L.GridLayer.intensityLayer = function (opt) {
        return new L.GridLayer.IntensityLayer(opt);
    };

    var _layer= L.GridLayer.intensityLayer(opt);
    _layer.addTo(map);

    function fillTile(tile, tileSize, tileBounds, pixelRatio, grid, done) {
        var ctx = tile.getContext('2d');
        // ctx.fillStyle = 'green';
        // ctx.fillRect(0, 0, tileSize.x, tileSize.y);

        var imageData = ctx.getImageData(0, 0, tileSize.x, tileSize.y);
        var data = imageData.data;  // layout: [r, g, b, a, r, g, b, a, ...]

        var distancePerPixelX = (tileBounds.maxx - tileBounds.minx) / tileSize.x;
        var distancePerPixelY = (tileBounds.maxy - tileBounds.miny) / tileSize.y;

        function getCoordFromPixel(x, y) {
            return [
                tileBounds.minx + distancePerPixelX * (x),
                tileBounds.maxy - distancePerPixelY * (y),
            ];
        }

        function setColorToPixel(x, y, rgba) {
            var i = (y * tileSize.x + x) * 4;
            data[i] = rgba[0];
            data[i + 1] = rgba[1];
            data[i + 2] = rgba[2];
            data[i + 3] = 255;
        }

        for (var y = 0; y < tileSize.y; y+=2) {
            for (var x = 0; x < tileSize.x; x += 2) {
                var coord = getCoordFromPixel(x, y);
                var rgb = grid.getColor(coord);
                // var rgb = [0, 255, 0];
                if (rgb != null) {
                    setColorToPixel(x, y, rgb);
                    if (x + 1 < tileSize.x)
                        setColorToPixel(x + 1, y, rgb);
                    if (y + 1 < tileSize.y)
                        setColorToPixel(x, y + 1, rgb);
                    if (x + 1 < tileSize.x && y + 1 < tileSize.y)
                        setColorToPixel(x + 1, y + 1, rgb);
                }
            }
        }

        ctx.putImageData(imageData, 0, 0);
        // const tileName = tile.getAttribute('name')
        // console.log('Tile ' + tileName + ' created!!');
        if(done)
            done(undefined, tile);
    }

    return {
        changeSourceGrid: function (grid_to_change) {
            grid = grid_to_change;
            if (pendingTiles.length > 0) {
                for (const t of pendingTiles) {
                    fillTile(t.tile, t.tileSize, t.tileBounds, 1, grid, t.done);
                }
                pendingTiles.clear();
            } else
                _layer.redraw();
            // console.log("Change grid.");
        },
        show: function () {
            if(!map.hasLayer(_layer))
                _layer.addTo(map);
        },
        hide: function () {
            map.removeLayer(_layer);
        },

    };
}
