<template>
    <div>
        <div style="color: blue; font-size: 15px"><b>{{value != null && value.properties.name}}</b></div>
        <v-divider style="margin: 5px 0px"></v-divider>

        <table>
            <tr v-for="item in items" :key="item[1]">
                <td class="first_cell">{{item[0]}}</td>
                <td calss="second_cell">
                    {{get_value(item[1], item[2])}}
                    <img v-if="item[3]" src="/data/arrow-up.svg" width="12" height="12" :style="wind_dir_style"/>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    export default {
        name: "StationPopupBox",
        props: ["value"],
        data: function () {
            return {
                items: [
                    ["바람", 'wind_speed10', 'm/s', true],
                    ["기온", 'temperature', '°C', false],
                    ["습도", 'humidity', '%', false],
                    ["강수량", 'rain_15m', 'mm/h', false]
                ]
            };
        },
        computed: {
            wind_dir_style: function () {
                var wind_dir = this.value != null && this.value.properties.wind_dir10 || null;

                if (wind_dir == null)
                    return {display: 'none'};
                else {
                    wind_dir = parseFloat(wind_dir);
                    wind_dir += 180;
                    return {transform: 'rotate(' + wind_dir + 'deg)'};
                }
            }
        },
        methods: {
            get_value: function (field_name, unit) {
                if (this.value == null)
                    return '정보없음';
                const field_value = this.value.properties[field_name];
                if (field_value == null)
                    return '정보없음';

                return field_value + unit;
            },
        }
    }
</script>

<style scoped>
    .first_cell {
        text-align: right;
        padding-right: 15px;
        color: blue;
    }
    .second_cell{
        text-align: left;
    }
</style>