import {HUMIDITY, RAIN, TEMP, WIND} from "@/value";
import {calc_sq_distance_of_points, linearInterpolation} from "@/logic";
import L from 'leaflet';
import {Point} from "leaflet/dist/leaflet-src.esm";
import StationPopupBox from "@/components/StationPopupBox";
import Vue from 'vue'


export function addStationLayer(map, opt) {
    var field_name;
    var station_grid = null;

    function setFieldName(overlay_type) {
        switch (overlay_type) {
            case WIND:
                field_name = "wind_speed10";
                break;
            case TEMP:
                field_name = "temperature";
                break;
            case HUMIDITY:
                field_name = "humidity";
                break;
            case RAIN:
                field_name = "rain_15m";
                break;
        }
    }

    setFieldName(opt.overlay_type);

    var stationStyle = {
        radius: 3,
        fillColor: "#ff7800",
        color: "#000",
        weight: 1,
        opacity: 1,
        fillOpacity: 1
    };

    function filter_by_distance(station, candidates, distance) {
        return candidates.filter(function (candidate) {
            const d = Math.sqrt(calc_sq_distance_of_points(station.geometry.coordsInPixel, candidate.geometry.coordsInPixel));
            return d >= distance;
        });
    }

    function buildPopupContent(feature) {
        var instance = new (Vue.extend(StationPopupBox))({propsData: {value: feature}})
        instance.$mount();
        return instance.$el;
    }

    var _layer = L.geoJSON(null, {
        zIndex: opt.zIndex,
        pointToLayer: function (feature, coordinates) {
            var value = feature.properties[field_name];
            var label = value != null && value.toString() || "";
            var latlng = L.CRS.EPSG3857.unproject(new Point(coordinates.lng, coordinates.lat));
            var size = opt.is_mobile ? 6 : 5;
            var labelClass = opt.is_mobile ? 'mobile-station-label' : 'station-label';
            var html = '<div class='+labelClass+'>'+label+'</div>';

            var icon = L.divIcon(
                {
                    html,
                    className: 'red-dot',
                    iconSize: [size, size]
                });

            // var popup_content = makePopupContent(feature);

            var marker = L.marker(latlng, {icon});
            var popup = new L.popup({
                minWidth: 150,
                autoPan: false,
                keepInView: true,
                closeOnClick: false
            }, marker).setContent(() => {return buildPopupContent(feature)});
            return marker.bindPopup(popup);
            // return L.circleMarker(latlng, stationStyle)
            //     .bindTooltip(label, {permanent: true, className: "station-label", opacity: 1}).openTooltip();
        }
    }).addTo(map);

    return {
        setOverlayType: function (overlay_type) {
            setFieldName(overlay_type);
            this.redraw();
        },

        setStationGrid: function (station_grid_) {
            station_grid = station_grid_;
            this.redraw();
        },

        redraw: function () {
            if (station_grid == null)
                return;

            _layer.clearLayers();

            var zoom = map.getZoom();

            const MIN_ZOOM = 7;
            const MAX_ZOOM = 10;
            const MAX_DISTANCE = 40;
            const MIN_DISTANCE = 20;

            var mapPixelBounds = map.getPixelBounds();
            var features = station_grid.getStations();

            var candidates = features.filter(feature => {
                var coordinates = feature.geometry.coordinates;
                var latlng = L.CRS.EPSG3857.unproject(new Point(coordinates[0], coordinates[1]));
                var pixelCoords = map.project(latlng);
                feature.geometry.coordsInPixel = pixelCoords;
                return mapPixelBounds.contains(pixelCoords) &&
                    feature.properties[field_name] != null;
            });

            let min_distance_in_pixel = linearInterpolation(MIN_ZOOM, MAX_ZOOM, MAX_DISTANCE, MIN_DISTANCE, zoom);

            candidates.sort(function (l, r) {
                const l_value = l.properties[field_name];
                const r_value = r.properties[field_name];
                return l_value - r_value;
            });

            var selected = new Set();

            while (candidates.length > 0) {
                var station = candidates.pop();
                selected.add(station.id);
                candidates = filter_by_distance(station, candidates, min_distance_in_pixel);
            }

            var selectedFeatures = features.filter(feature => selected.has(feature.id));
            _layer.addData(selectedFeatures);
        },
        show: function () {
        },
        hide: function () {
        },
    }
}