
import Heap from "collections/heap";
import {calc_sq_distance} from "@/logic";

export function make_station_grid(aws_data, res) {
    function extend_bbox(left, bottom, right, top, res) {
        return [
            Math.floor(left / res - 1) * res,
            Math.floor(bottom / res - 1) * res,
            (Math.floor(right / res) + 2) * res,
            (Math.floor(top / res) + 2) * res
        ]
    }

    var features = aws_data.features;

    var bbox = {
        left: Number.MAX_VALUE,
        bottom: Number.MAX_VALUE,
        right: 0,
        top: 0,
    };

    for (var i = 0; i < features.length; i++) {
        var entry = features[i];
        var coordinates = entry.geometry.coordinates;

        if (coordinates[0] < bbox.left)
            bbox.left = coordinates[0];
        if (coordinates[0] > bbox.right)
            bbox.right = coordinates[0];
        if (coordinates[1] > bbox.top)
            bbox.top = coordinates[1];
        if (coordinates[1] < bbox.bottom)
            bbox.bottom = coordinates[1];
    }

    var extent = extend_bbox(bbox.left, bbox.bottom, bbox.right, bbox.top, res);
    bbox.left = extent[0];
    bbox.bottom = extent[1];
    bbox.right = extent[2];
    bbox.top = extent[3];

    var grid_width = Math.floor((bbox.right - bbox.left) / res);
    var grid_height = Math.floor((bbox.top - bbox.bottom) / res);

    var stations_in_grid = new Array(grid_height);
    for (var iy = 0; iy < grid_height; iy++) {
        stations_in_grid[iy] = new Array(grid_width);
    }

    function get_grid_x_index(x_coordinate, keep_org) {
        var i = (x_coordinate - bbox.left) / res;
        return keep_org ? i : Math.floor(i);
    }

    function get_grid_y_index(y_coordinate, keep_org) {
        var i = (y_coordinate - bbox.bottom) / res;
        return keep_org ? i : Math.floor(i);
    }

    for (i = 0; i < features.length; i++) {
        entry = features[i];
        var ix = get_grid_x_index(entry.geometry.coordinates[0]);
        iy = get_grid_y_index(entry.geometry.coordinates[1]);

        var grid_entries = stations_in_grid[iy][ix];
        if (grid_entries === undefined) {
            stations_in_grid[iy][ix] = [entry];
        } else {
            grid_entries.push(entry);
        }
    }

    function in_the_grid(ix, iy) {
        return iy >= 0 && iy < grid_height && ix >= 0 && ix < grid_width;
    }

    function getStationsInRadius(coordinate, search_radius, max_neighbors) {
        var left = coordinate[0] - search_radius;
        var right = coordinate[0] + search_radius;
        var bottom = coordinate[1] - search_radius;
        var top = coordinate[1] + search_radius;

        var sq_search_radius = search_radius * search_radius;

        var i_left = get_grid_x_index(left);
        var i_right = get_grid_x_index(right);
        var i_bottom = get_grid_y_index(bottom);
        var i_top = get_grid_y_index(top);

        var result = new Heap(null, null, function (a, b) {
            return a.sq_distance - b.sq_distance;
        });
        for (var iy = i_bottom; iy <= i_top; iy++) {
            for (var ix = i_left; ix <= i_right; ix++) {
                if (!in_the_grid(ix, iy))
                    continue;
                var stations = stations_in_grid[iy][ix];
                if (stations === undefined)
                    continue;

                for (var ie = 0; ie < stations.length; ie++) {
                    var station = stations[ie];
                    var sq_distance = calc_sq_distance(coordinate, station.geometry.coordinates);

                    if (sq_distance > sq_search_radius)
                        continue;

                    if (result.length < max_neighbors || sq_distance < result.max().sq_distance) {
                        if (result.length >= max_neighbors)
                            result.pop();
                        result.push(
                            {
                                'sq_distance': sq_distance,
                                station
                            });
                    }
                }
            }
        }

        return result.toArray();
    }

    return {
        bbox: bbox,
        aws_data: aws_data,

        getStationsInRadius: function(coordinate, search_radius, max_neighbors) {
            return getStationsInRadius(coordinate, search_radius, max_neighbors);
        },

        getStations: function () {
            return features;
        },

        getSize: function() {
            return [grid_width, grid_height];
        },

        getGridXIndex(x_coordinate) {
            return get_grid_x_index(x_coordinate);
        },

        getGridYIndex(y_coordinate) {
            return get_grid_y_index(y_coordinate);
        },

        getIndexFromCoord(coordinates, keep_org) {
            return [get_grid_x_index(coordinates[0], keep_org), get_grid_y_index(coordinates[1], keep_org)];
        },

        getCoordFromIndex(ix, iy) {
            return [bbox.left + ix * res, bbox.bottom + iy * res];
        },

        inTheGrid(ix, iy) {
            return in_the_grid(ix, iy);
        },

    }
}
