<template>
    <v-app>
        <!--        <v-app-bar-nav-icon id="btn_menu" v-show="!showDrawer" @click.stop="showDrawer=true"></v-app-bar-nav-icon>-->
        <v-icon color="white" id="btn_main_menu"
                :style="is_mobile?'right:10px':'left:10px'"
                v-show="!showDrawer"
                @click.stop="showDrawer=true">
            mdi-menu
        </v-icon>
        <v-navigation-drawer
                id="main_menu"
                width="200"
                v-model="showDrawer"
                :right="is_mobile"
                :style="is_mobile?'background: rgba(0,0,0,0.5);':''"
                app
        >
            <v-btn id="btn_close" :style="is_mobile?'left: -45px':'right:-45px'" small fab dark color="orange" @click.stop="showDrawer=false">
                <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-list dense>
                <v-list-item :color="is_mobile?'white':'black'" link href="https://weather-now.tistory.com/1" target="_blank">
                    <v-list-item-action>
                        <v-icon :style="itemColor">mdi-help-circle-outline</v-icon>
                    </v-list-item-action>
                    <v-list-item-content :style="itemColor">
                        <v-list-item-title>도움말</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <v-main>
            <Map :params="params" :is_mobile="is_mobile"></Map>
        </v-main>
    </v-app>
</template>

<script>
    import Map from "@/components/Map";
    import {WIND} from "@/value";

    export default {
        name: 'App',
        components: {
            Map
        },
        data: () => ({
            selectedItem: 0,
            params: {
                t: WIND,
                lo: null,
                la: null,
                z: null,
                query_dt: null,
            },
            date: 0,
            showDrawer: false,
        }),
        created: function () {
            this.params = Object.assign(this.params, this.parseUrlHash());
        },
        computed: {
            itemColor() {
                var color = this.is_mobile ? 'white' : 'black';
                return {color};
            },
            is_mobile() {
                function detectMobileBrowsers() {
                    let check = false;
                    (function (a) {
                        // eslint-disable-next-line no-useless-escape
                        if (/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(a) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(a.substr(0, 4))) check = true;
                    })(navigator.userAgent || navigator.vendor || window.opera);
                    return check;
                }

                if (this.params.m === 0)
                    return false;
                else if (this.params.m === 1)
                    return true;
                return detectMobileBrowsers();
            }
        }
        ,
        methods: {
            parseUrlHash: function () {
                var params = {};

                var hash_url = window.location.hash.substr(1);

                if (hash_url.length > 0) {
                    hash_url.split('&').forEach(function (param) {
                        var [k, v] = param.split('=');
                        params[k] = Number(v);
                    });
                }

                return params;
            },
        },
    }
</script>

<style>
    body {
        padding: 0;
        margin: 0;
    }

    html, body, #app {
        height: 100%;
        width: 100vw;
    }

    #main_menu {
        z-index: 5000;
        overflow: visible;
        /*background: rgba(0, 0, 0, 0.8);*/
    }

    #btn_close {
        position: absolute;
        top: 10px;
    }

    #btn_main_menu {
        position: fixed;
        top: 10px;
        z-index: 5000;
        text-shadow: 0 0 4px black;
        font-size: 30px;
