<template>
    <div style="width: 100vw">
        <div id="timebox">
            <div class="round-box">
                <slot :value="value">

                </slot>
            </div>
            <div class="reverse-triangle"></div>
        </div>
        <div id="slider" @touchstart="onTouchStart" @touchend="onTouchEnd" @scroll="onScroll" ref="slider">
            <div class="time-entry dark-box" style="width: 50%">
                &#0020; <!--스페이스를 넣지 않으면, div가 위로 올라간다. &nbsp;로 했는데, 결과에는 없어져서 이 방법을 이용-->
            </div>
            <div class="time-entry" v-for="i in 24"
                 :key="i"
                 :class="i % 2 === 0 ? 'dark-box' : 'box'"
            >
                &nbsp;&nbsp;{{25 - i}} 시간 전
            </div>
            <div class="time-entry box" style="width: 50%">
                &#0020;
            </div>
        </div>
        <v-btn id="btn-refresh" @click="onRefresh" dark depressed color="orange">
            <v-icon medium>mdi-refresh</v-icon>
        </v-btn>
    </div>

</template>

<script>
    export default {
        name: "MobileTimeSlider",
        mounted() {
            this.$refs.slider.scrollLeft = this.$refs.slider.scrollWidth;
            this.maxScroll = this.$refs.slider.scrollLeft;
            this.scrollLeft = this.$refs.slider.scrollLeft;
        },
        data: () => ({
            maxScroll: null,
            scrollLeft: null,
            isScrolling: null,
            touching: false,
        }),
        methods: {
            onScroll(e) {
                // console.log('Scrolling : ' + e.target.scrollLeft);
                this.scrollLeft = e.target.scrollLeft;

                if (this.isScrolling !== null)
                    window.clearTimeout(this.isScrolling);
                this.isScrolling = setTimeout(() => {
                    // console.log('Scrolling has stopped.');
                    if (!this.touching)
                        this.$emit('scrollend', this.value);
                }, 1000);
            },
            onTouchStart(e) {
                this.touching = true;
            },
            onTouchEnd(e) {
                this.touching = false;
                if (this.isScrolling === null)
                    this.$emit('scrollend', this.value);
            },
            onRefresh() {
                if (this.scrollLeft === this.maxScroll) {
                    this.$emit('scrollend', this.value);
                } else
                    this.$refs.slider.scrollLeft = this.maxScroll;
            }
        },
        computed: {
            value: function () {
                return 100 * this.scrollLeft / this.maxScroll;
            }
        },
    }
</script>

<style scoped>
