        overflow-x: scroll;
        user-select: none;
        -ms-overflow-style: none; /* Internet Explorer 10+ */
        scrollbar-width: none; /* Firefox */
        -webkit-overflow-scrolling: touch;
        color: white;
    }

    #slider::-webkit-scrollbar {
        display: none; /* Safari and Chrome */
    }

    .box {
        background: rgba(63, 61, 80, 0.76);;
    }

    .dark-box {
        background: rgba(0, 0, 0, 0.63);;
    }

    .time-entry {
        display: inline-block;
        user-select: none;
        width: 120px;
        white-space: nowrap;
        height: 32px;
    }

    #timebox {
        position: absolute;
        top: -32px;
        left: 50%;
    }

    #btn-refresh {
        position: fixed;
        bottom: 3px;
        right: 3px;
        border-radius: 50%;
        min-width: 32px;
        width: 32px;
        height: 32px;
    }

    .round-box {
        position: absolute;
        height: 26px;
        color: white;
        top: -5px;
        left: -32px;
        background-color: orange;
        border-radius: 0.5em;
        white-space: nowrap;
        font-size: 18px;
        padding: 0px 0.8em 0px;
    }

    .reverse-triangle {
        width: 0;
        height: 0;
        position: absolute;
        left: -8px;
        bottom: -30px;
        border-color: orange transparent transparent transparent !important;
        border-left: 8px solid;
        border-right: 8px solid;
        border-top: 13px solid;
        background-color: transparent !important;
    }

</style>