import Vue from 'vue';
import {VSlider} from "vuetify/lib/components/VSlider";
import {convertToUnit} from "vuetify/lib/util/helpers";
import {VScaleTransition} from "vuetify/lib/components/transitions";


export default Vue.extend(VSlider).extend(
    {
        name: 'v-custom-slider',
        data: () => ({
            cursorX: 0,
            cursorVisible: 'hidden',
            valueOnCursor: 0,
        }),
        // created() {
        //     this.$on('change', () => this.cursorVisible = 'hidden');
        // },
        computed: {
            cursorBoxStyle () {
                const diff = this.internalValue - this.valueOnCursor;
                const top =  (-2 <= diff && diff <= 2) ? -38 : -18;
                return {
                    position: 'absolute',
                    visibility: this.isActive ? 'hidden' : this.cursorVisible,
                    left: this.cursorX+ 'px',
                    top: top + 'px',
                    width: 'auto',
                    'pointer-events': 'none'
                }
            }
        },
        methods: {
            callSuper(methodName, arg1) {
                this.constructor.super.options.methods[methodName].call(this, arg1);
            },
            genDefaultSlot() {
                const children = [this.genLabel()];
                children.push(this.genCursorBox(this.valueOnCursor));
                const slider = this.decorateSlider(this.genSlider());
                this.inverseLabel ? children.unshift(slider) : children.push(slider);
                children.push(this.genProgress());
                return children;
            },
            onSliderClick(e) {
                this.cursorVisible = 'hidden';
                this.callSuper('onSliderClick', e);
            },

            decorateSlider(slider) {
                slider.data.ref = 'slider';
                slider.data.on['mousemove'] =  this.onMouseMoveInSlider;
                slider.data.on['mouseenter'] =  this.onMouseEnterIntoSlider;
                slider.data.on['mouseleave']  = this.onMouseLeaveFromSlider;
                return slider;
            },

            genThumbLabel(content) {
                const size = convertToUnit(this.thumbSize);
                return this.$createElement(VScaleTransition, {
                    props: {
                        origin: 'bottom center'
                    }
                }, [this.$createElement('div', {
                    staticClass: 'v-slider__thumb-label-container',
                    directives: [{
                        name: 'show',
                        value: this.isFocused || this.isActive || this.thumbLabel === 'always'
                    }]
                }, [this.$createElement('div', this.setBackgroundColor(this.computedThumbColor, {
                    staticClass: 'v-slider__thumb-label',
                    style: {
                        'white-space': 'nowrap',
                        'border-radius': '.5em',
                        'bottom': '1em',
                        'transform': 'none',
                    }
                }), [this.$createElement('div', content)])])]);
            },
            genThumb() {
                return this.$createElement('div', this.setBackgroundColor(this.computedThumbColor, {
                    staticClass: 'v-custom-slider__thumb'
                }));
            },
            genCursorBoxContent(value) {
                return this.$scopedSlots['cursor-box-content'] ? this.$scopedSlots['cursor-box-content']({value}) :
                    [this.$createElement('span', [String('fill me')])];
            },
            genCursorBox(value) {
                return this.$createElement('div', {
                    style: this.cursorBoxStyle
                }, this.genCursorBoxContent(value));
            },
            onMouseEnterIntoSlider() {
                // console.log('mouseenter');
                this.cursorVisible = 'visible';
            },

            onMouseMoveInSlider(e) {
                this.cursorVisible = 'visible';
                const rect = this.$refs.slider.getBoundingClientRect();
                // console.log(rect.left + ', ' + e.clientX);
                this.cursorX = e.clientX - rect.left; //x position within the element.
                const width = rect.right - rect.left;
                this.valueOnCursor = Math.round((100 * this.cursorX) / width);
            },
            onMouseLeaveFromSlider() {
                // console.log('mouseleave');
                this.cursorVisible = 'hidden';
            },
        }
    }
);