<template>
    <div :class="is_mobile ? 'mobile-side-menu' : 'side-menu'">
        <div v-if="is_mobile" :style="getPosition" :class="showTitle ? 'name visible':'name hidden'">{{items[value].text}}</div>
        <v-list rounded dense style="background-color: transparent;" class="custom">
            <v-list-item-group :mandatory="true" v-bind:value="value" v-on:change="onChange">
                <v-list-item
                        v-for="item in items"
                        :key="item.key"
                >
                    <!--                            <v-list-item-title v-text="item.text"></v-list-item-title>-->
                    <v-chip v-if="!is_mobile" style="margin-right: 5px"><b>{{item.text}}</b></v-chip>
                    <!--                            <v-list-item-icon>-->
                    <v-avatar size="30">
                        <v-icon v-text="item.icon" medium></v-icon>
                    </v-avatar>
                    <!--                            </v-list-item-icon>-->
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </div>
</template>

<script>
    import {HUMIDITY, RAIN, TEMP, WIND} from "@/value";

    export default {
        name: "SideMenu",
        props: ["value", "is_mobile"],
        data: () => ({
            showTitle: false,
            items: [
                {text: '풍속', key: WIND, icon: 'mdi-weather-windy'},
                {text: '기온', key: TEMP, icon: 'mdi-thermometer'},
                {text: '강수', key: RAIN, icon: 'mdi-weather-pouring'},
                {text: '습도', key: HUMIDITY, icon: 'mdi-water-percent'},
            ],
        }),
        methods: {
            onChange(new_val) {
                this.$emit('input', new_val);
                if(this.is_mobile) {
                    this.showTitle = true;
                    setTimeout(() => {
                        this.showTitle = false;
                    }, 1000);
                }
            }
        },
        computed: {
            getPosition: function() {
                return {
                    right: '53px',
                    bottom: 56 + 44 * (3 - this.value) + 'px'
                }
            }
        }
    }
</script>

<style scoped>
    .side-menu {
        position: fixed;
        right: 20px;
        top: 50px;
    }

    .mobile-side-menu {
        position: fixed;
        right: -8px;
        bottom: 40px;
    }

    .custom .theme--light.v-chip:not(.v-chip--active) {
        background: rgba(0,0,0,0.45);
        color: white;
    }
    .custom .v-list-item--active .theme--light.v-chip:not(.v-chip--active) {
        background: orange;
        color: white;
    }

    .custom .theme--light.v-icon {
        background: rgba(0,0,0,0.45);
        color: white;
    }
    .custom .v-list-item--active .theme--light.v-icon {
        background: orange;
        color: white;
    }
    .v-list-item--active{
        background: transparent;
    }
    .custom .theme--light.v-list-item--active:hover::before, .custom .theme--light.v-list-item--active::before {
        opacity: 0;
    }

    .mobile-side-menu .custom .theme--light.v-chip:not(.v-chip--active) {
        background: rgba(0,0,0,0.45);
        color: white;
        visibility: hidden;

    }
    .mobile-side-menu .custom .v-list-item--active .theme--light.v-chip:not(.v-chip--active) {
        background: orange;
        color: white;
        visibility: visible;
    }
    .name {
        background: rgba(63, 61, 80, 0.76);
        /*background: orange;*/
        width: 50px;
        color: white;
        border-radius: 15px;
        padding: 0 10px;
        margin-left: 14px;
        text-align: center;
        position: fixed;
    }
    .visible {
        visibility: visible;
        opacity: 1;
    }
    .hidden {
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s 2s, opacity 2s linear;
    }
</style>