import Vue from 'vue';

export function padZero(n) {
    const v = n.toString();
    if (v.length < 2)
        return '0' + v;
    return v;
}

export function parseStrToDate(date_str) {
    var start = 0;
    var year = parseInt(date_str.substring(start, start + 4)); start += 5;
    var month = parseInt(date_str.substring(start, start + 2)) - 1; start += 3;
    var day = parseInt(date_str.substring(start, start + 2)); start += 3;
    var hour = parseInt(date_str.substring(start, start + 2)); start += 3;
    var minute = parseInt(date_str.substring(start, start + 2));

    return new Date(year, month, day, hour, minute);
}

export function calcDate (now, v) {
    // v: 0 <= v <= 100
    let a_day_in_minutes = 24 * 60;
    let minutes_ago = a_day_in_minutes * (100 - v) / 100;
    return new Date(now.getTime() - minutes_ago * 60 * 1000);
}

export function dateToStr(end_dt, dt) {
    let the_day = '';
    if (end_dt.getDate() !== dt.getDate()) {
        // the_day = dt.getDate() + '일 ';
        the_day = '어제 '
    }
    return the_day + padZero(dt.getHours()) + ':' + padZero(dt.getMinutes());
}

export function convertToTime (now, v) {
    if(v >= 100)
        return '현재';
    const dt = calcDate(now, v);
    return dateToStr(now, dt);
}

export function convertTimeToSliderPos(endDt, dt) {
    const time_diff = (endDt.getTime() - dt.getTime()) / 1000;
    let a_day_in_seconds = 24 * 60 * 60;

    let pos = 100 - time_diff / a_day_in_seconds * 100;
    if(pos > 100)
        pos = 100;
    else if(pos < 0)
        pos = 0;
    return pos;
}

// https://en.wikipedia.org/wiki/Inverse_distance_weighting#Modified_Shepard's_method
export function modified_shepard_weighting(R) {
    return function (sq_distance) {
        var d = Math.sqrt(sq_distance);
        return Math.pow((R - d) / (R * d), 2);
    };
}

export function calc_sq_distance(from, to) {
    var w = to[0] - from[0];
    var h = to[1] - from[1];

    return w * w + h * h;
}

export function calc_sq_distance_of_points(from, to) {
    var w = to.x - from.x;
    var h = to.y - from.y;

    return w * w + h * h;
}

export function linearInterpolation(x0, x1, y0, y1, x) {
    if(x <= x0)
        return y0;
    else if(x >= x1)
        return y1;
    else {
        const a = (y1 - y0) / (x1 - x0);
        return (x - x0) * a + y0;
    }
}



Vue.prototype.convertToTime = convertToTime;
