import {Scala, Vector} from "@/value";
import {humidityToColor, rainToColor, tempToColor, windVectorToColor} from "@/color_scale";
import {HUMIDITY, RAIN, TEMP, WIND} from "@/value";
import {calc_sq_distance, modified_shepard_weighting} from "@/logic";


export function interpolate_to_grid(station_grid, max_search_radius, max_neighbors, overlay_type) {
    function windComponent(speed, dir) {
        if (speed == null || dir == null)
            return null;
        var radians = dir * Math.PI / 180;
        return [-speed * Math.sin(radians), speed * Math.cos(radians)];
    }

    var init_func;
    var extract_func;
    var color_func;

    switch (overlay_type) {
        case WIND:
            init_func = () => Vector.create([0, 0]);
            extract_func = function (station) {
                return Vector.create(windComponent(station.properties.wind_speed10, station.properties.wind_dir10));
            };
            color_func = windVectorToColor;
            break;
        case TEMP:
            init_func = () => Scala.create(0);
            extract_func = function (station) {
                return Scala.create(station.properties.temperature);
            };
            color_func = tempToColor;
            break;
        case HUMIDITY:
            init_func = () => Scala.create(0);
            extract_func = function (station) {
                return Scala.create(station.properties.humidity);
            };
            color_func = humidityToColor;
            break;
        case RAIN:
            init_func = () => Scala.create(0);
            extract_func = function (station) {
                return Scala.create(station.properties.rain_15m);
            };
            color_func = rainToColor;
            break;
    }

    var grid_width;
    var grid_height;
    [grid_width, grid_height] = station_grid.getSize();
    // grid_width = grid_size[0];
    // grid_height = grid_size[1];

    function interpolate_from_values(entries, weight_func) {
        var total_weight = 0;

        var weights = entries.map(function (e) {
            var w = weight_func(e.sq_distance);
            total_weight += w;
            return w;
        });

        var interpolated_value = init_func();

        for (var i = 0; i < entries.length; i++) {
            var w = weights[i] / total_weight;
            interpolated_value.weighted_sum(w, entries[i].value);
        }

        return interpolated_value;
    }

    function bilinearInterpolate(x, y, v00, v10, v01, v11) {
        var interpolated_value = init_func();

        var rx = (1 - x);
        var ry = (1 - y);

        // return v00 * rx * ry + v10 * x * ry + v01 * rx * y + v11 * x * y;
        interpolated_value.weighted_sum(rx * ry, v00);
        interpolated_value.weighted_sum(x * ry, v10);
        interpolated_value.weighted_sum(rx * y, v01);
        interpolated_value.weighted_sum(x * y, v11);

        return interpolated_value;
    }

    var weight_func = modified_shepard_weighting(max_search_radius);

    function interpolate_from_stations(coord) {
        var entries = station_grid.getStationsInRadius(coord, max_search_radius, max_neighbors);
        if (entries.length === 0)
            return null;

        entries = entries.map(function (e) {
            e.value = extract_func(e.station);
            return e;
        }).filter(e => e.value != null);
        if (entries.length === 0)
            return null;
        return interpolate_from_values(entries, weight_func);
    }

    var grid_data = new Array(grid_width + 1);
    for (let ix = 0; ix <= grid_width; ix++) {
        grid_data[ix] = new Array(grid_height + 1);
        for (let iy = 0; iy <= grid_height; iy++) {
            var coord = station_grid.getCoordFromIndex(ix, iy);
            grid_data[ix][iy] = interpolate_from_stations(coord);
        }
    }

    return {
        getInterpolatedValue: function (coord) {
            var x, y, x0, y0, v00, v10, v01, v11;
            [x, y] = station_grid.getIndexFromCoord(coord, true);
            if (!station_grid.inTheGrid(x, y))
                return null;

            x0 = Math.floor(x);
            y0 = Math.floor(y);
            if ((v00 = grid_data[x0][y0]) &&
                (v10 = grid_data[x0 + 1][y0]) &&
                (v01 = grid_data[x0][y0 + 1]) &&
                (v11 = grid_data[x0 + 1][y0 + 1]))
                return bilinearInterpolate(x - x0, y - y0, v00, v10, v01, v11).value;
            else
                return null;
        },

        getColor: function (coord) {
            var interpolated_value = this.getInterpolatedValue(coord);
            if (interpolated_value == null)
                return null;
            return color_func(interpolated_value);
        },

        getInterpolatedValueStr: function (coord) {
            var v = this.getInterpolatedValue(coord);

            if (v == null)
                return null;
            // v = v.value;
            switch (overlay_type) {
                case WIND:
                    var wind_speed = Math.sqrt(v[0] * v[0] + v[1] * v[1]);
                    return wind_speed.toFixed(1) + 'm/s';
                case TEMP:
                    return v.toFixed(1) + '°C';
                case HUMIDITY:
                    return v.toFixed(1) + '%';
                case RAIN:
                    return v.toFixed(1) + 'mm/h';
            }
            return null;
        },
    }
}