/**
 * @return {null}
 */

export var WIND = 0;
export var TEMP = 1;
export var RAIN = 2;
export var HUMIDITY = 3;

export function Vector(value) {
    this.value = value;
}

Vector.prototype.multiply = function (w) {
    return new Vector([this.value[0] * w, this.value[1] * w]);
};

Vector.prototype.add = function (o) {
    return new Vector([this.value[0] + o.value[0], this.value[1] + o.value[1]]);
};

Vector.prototype.weighted_sum = function (w, other) {
    this.value[0] += w * other.value[0];
    this.value[1] += w * other.value[1];
};

Vector.create = function (value) {
    if (value == null)
        return null;
    return new Vector(value);
};



/**
 * @return {null}
 */
export function Scala(value) {
    this.value = value;
}

Scala.prototype.multiply = function (w) {
    return new Scala(this.value * w);
};

Scala.prototype.add = function (o) {
    return new Scala(this.value + o.value);
};

Scala.prototype.weighted_sum = function (w, other) {
    this.value += w * other.value;
};


Scala.create = function (value) {
    if (value == null)
        return null;
    return new Scala(value);
};
