import L from 'leaflet'
import {linearInterpolation} from "@/logic";
import {pixelToMeter, resolution} from "@/globalmaptiles";

const INITIAL_TTL = 100;
const NUMBER_OF_PARTICLES = 500;
const PARTICLE_FADING = 0.95;


export function addAnimationLayer(map, opt) {
    var fading = PARTICLE_FADING;
    var ttl = INITIAL_TTL;

    var particle_cnt = null;
    var grid_width = null;
    var max_particle_cnt_in_cell = null;

    var PARTICLE_SIZE = 1;
    var particles = new Array(NUMBER_OF_PARTICLES);

    function randrange(start, stop) {
        return Math.random() * (stop - start) + start;
    }

    function getIntersection(bounds1, bounds2) {
        if (bounds1.overlaps(bounds2)) {
            var left = Math.max(bounds1.min.x, bounds2.min.x);
            var right = Math.min(bounds1.max.x, bounds2.max.x);
            var top = Math.min(bounds1.max.y, bounds2.max.y);
            var bottom = Math.max(bounds1.min.y, bounds2.min.y);
            return L.bounds(L.point(left, top), L.point(right, bottom));
        } else
            return null;
    }

    function coordsToPoint(crs, zoom, coords) {
        var scale = crs.scale(zoom);
        return crs.transformation._transform(coords, scale);
    }

    function pointToCoords(crs, zoom, point) {
        var scale = crs.scale(zoom);
        return crs.transformation.untransform(point, scale);
    }

    function coordsToContainerPoint(map, coords) {
        var projectedPoint = coordsToPoint(map.options.crs, map._zoom, coords)._round();
        var layerPoint = projectedPoint._subtract(map.getPixelOrigin());
        return map.layerPointToContainerPoint(layerPoint);
    }

    function containerPointToCoords(map, point) {
        var layerPoint = map.containerPointToLayerPoint(point);
        var projectedPoint = layerPoint.add(map.getPixelOrigin());
        return pointToCoords(map.options.crs, map._zoom, projectedPoint);
    }

    L.Layer.WindAnimationLayer = L.Layer.extend({
        mapSize: null,
        station_grid: null,
        wind_grid: null,
        gridBounds: null,
        _requestedId: null,
        _canvas: null,
        _ctx: null,
        particle_color: 'white',
        do_init: true,

        options: {
            pane: 'tilePane'
        },

        resizeCanvas: function (width, height) {
            this._canvas.width = width;
            this._canvas.height = height;
        },

        onAdd: function (map) {
            this._map = map;
            var pane = map.getPane(this.options.pane);
            this._canvas = L.DomUtil.create('canvas');
            this._ctx = this._canvas.getContext('2d');

            pane.appendChild(this._canvas);
            this._resetPostion();

            map.on('movestart', () => {
                this.stop();
            });
            map.on('moveend', () => {
                this._resetPostion();
                this.start();
            });
            map.on('zoomstart', () => {
                this.stop();
                this._ctx.clearRect(0, 0, this._canvas.width, this._canvas.height);
            });
        },

        _resetPostion: function () {
            var offset = this._map.containerPointToLayerPoint([0, 0]);
            L.DomUtil.setPosition(this._canvas, offset);
        },

        _resizeCanvas: function () {
            const mapSize = map.getSize();
            this.resizeCanvas(mapSize.x, mapSize.y);
        },

        start() {
            this.do_init = true;
            if (this._requestedId == null && this.wind_grid != null)
                this._requestedId = requestAnimationFrame(() => {this.render()});
        },

        stop: function () {
            if (this._requestedId != null) {
                cancelAnimationFrame(this._requestedId);
                this._requestedId = null;
            }
        },

        calcCellNo(coordinates) {
            let grid_ix = this.station_grid.getGridXIndex(coordinates[0]);
            let grid_iy = this.station_grid.getGridYIndex(coordinates[1]);

            return grid_iy * grid_width + grid_ix;
        },

        assignParticle(particle) {
            function randomizeCoordinates(bounds, coordinates) {
                var size = bounds.getSize();
                var topLeft = bounds.getTopLeft();
                coordinates[0] = Math.random() * size.x + topLeft.x;
                coordinates[1] = Math.random() * size.y + topLeft.y;
            }

            if (particle.cell_no != null)
                --particle_cnt[particle.cell_no];

            randomizeCoordinates(this.validCoordsBounds, particle.coordinates);
            let cell_no = this.calcCellNo(particle.coordinates);
            if (particle_cnt[cell_no] < max_particle_cnt_in_cell) {
                particle.cell_no = cell_no;
                // if (do_init)
                particle.ttl = Math.floor(randrange(ttl / 2, ttl));
                // else
                //     particle.ttl = ttl;
                particle_cnt[cell_no]++;
            } else
                particle.cell_no = null;
        },

        initParticleCnt(size) {
            var width = size[0];
            var height = size[1];
            grid_width = width;

            if (particle_cnt == null || particle_cnt.length !== height * width)
                particle_cnt = new Array(height * width);
            particle_cnt.fill(0);
        },

        initParticles(zoom) {
            max_particle_cnt_in_cell = Math.floor(linearInterpolation(7, 14, 2, 20, zoom));

            this.initParticleCnt(this.station_grid.getSize());

            for (var i = 0; i < particles.length; i++) {
                var particle = particles[i];
                if (particle === undefined) {
                    particle = {};
                    particles[i] = particle;
                }

                particle.ttl = -1;
                particle.cell_no = null;
                if (particle.coordinates === undefined)
                    particle.coordinates = [];
            }
        },

        advanceParticles() {
            const particleSize = Math.round(PARTICLE_SIZE * 2);
            let alpha = resolution(this._map.getZoom()) / 7;
            if(this._map.getZoom() === 6)
                alpha /= 2;
            var pixel = L.point([0, 0]);

            var mapSize = map.getSize();
            var mapCoordsBounds = L.bounds(
                containerPointToCoords(this._map, L.point(0, 0)),
                containerPointToCoords(this._map, L.point(mapSize.x, mapSize.y))
            );

            this.validCoordsBounds = getIntersection(mapCoordsBounds, this.gridBounds);
            if (this.validCoordsBounds == null)
                return;

            // function drawRect(ctx, bounds, color) {
            //     ctx.strokeStyle = color;
            //     ctx.beginPath();
            //     ctx.rect(bounds.min.x, bounds.min.y
            //         , bounds.getSize().x, bounds.getSize().y);
            //     ctx.stroke();
            // }
            //
            // var topLeft = coordsToContainerPoint(this._map, this.validCoordsBounds.getBottomLeft());
            // var bottomRight = coordsToContainerPoint(this._map, this.validCoordsBounds.getTopRight());
            // drawRect(this._ctx, L.bounds(topLeft, bottomRight), 'red');
            //
            // topLeft = coordsToContainerPoint(this._map, this.gridBounds.getBottomLeft());
            // bottomRight = coordsToContainerPoint(this._map, this.gridBounds.getTopRight());
            // drawRect(this._ctx, L.bounds(topLeft, bottomRight), 'blue');

            if (this.do_init) {
                this.initParticles(this._map.getZoom());
            }

            particles.forEach(particle => {
                if (particle.ttl < 0) {
                    this.assignParticle(particle);
                }

                if (particle.ttl >= 0) {
                    const wind_vector = this.wind_grid.getInterpolatedValue(particle.coordinates);
                    // const wind_vector = [5, 5];
                    if (wind_vector == null)
                        particle.ttl = -1;
                    else {
                        pixel.x = particle.coordinates[0];
                        pixel.y = particle.coordinates[1];

                        pixel = coordsToContainerPoint(this._map, pixel);

                        const wind_speed = Math.sqrt(wind_vector[0] * wind_vector[0] + wind_vector[1] * wind_vector[1]);
                        this._ctx.globalAlpha = 0.4 * Math.min(wind_speed, 7) / 7;

                        this._ctx.fillRect(
                            pixel.x,
                            pixel.y,
                            particleSize, particleSize
                        );
                        --particle.ttl;
                        // Compute new position

                        const MAX_WIND = 6;

                        var dx = wind_vector[0];
                        var dy = wind_vector[1];

                        if (wind_speed > MAX_WIND) {
                            dx = dx / wind_speed * MAX_WIND;
                            dy = dy / wind_speed * MAX_WIND;
                        }
                        dx = dx * alpha;
                        dy = dy * alpha;
                        particle.coordinates[0] += dx;
                        particle.coordinates[1] -= dy;
                    }
                }
            });
            this._ctx.globalAlpha = 1;
        },

        _fadeOut(fading) {
            this._ctx.globalAlpha = fading;
            this._ctx.globalCompositeOperation = 'destination-in';
            // this._ctx.globalCompositeOperation = 'multiply';
            this._ctx.fillRect(0, 0, this._canvas.width, this._canvas.height);
            this._ctx.globalAlpha = 1;
            this._ctx.globalCompositeOperation = 'source-over';
        },

        _resizeAndClearCanvasIfNeeded(width, height) {
            if (this._canvas.width !== width || this._canvas.height !== height) {
                this._canvas.width = width;
                this._canvas.height = height;
                // canvas.getContext('2d').clearRect(0, 0, canvas.width, canvas.height);
            } else if (this.do_init)
                this._ctx.clearRect(0, 0, this._canvas.width, this._canvas.height);
        },

        setWindGrid(station_grid_, wind_grid_) {
            this.station_grid = station_grid_;
            this.wind_grid = wind_grid_;
            this.gridBounds = L.bounds(
                L.point(station_grid_.bbox.left, station_grid_.bbox.bottom),
                L.point(station_grid_.bbox.right, station_grid_.bbox.top)
            );
            this.start();
        },

        setParticleColor(color) {
            this.particle_color = color;
        },

        render: function () {
            var size = this._map.getSize();
            this._resizeAndClearCanvasIfNeeded(size.x, size.y);
            if (this._ctx.fillStyle !== this.particle_color) {
                this._ctx.fillStyle = this.particle_color;
            }
            this._fadeOut(fading);
            this.advanceParticles();

            this._requestedId = requestAnimationFrame(() => {this.render()});
            this.do_init = false;
        },

    })
    ;

    L.Layer.windAnimationLayer = function (opt) {
        return new L.Layer.WindAnimationLayer(opt);
    };

    var _layer = L.Layer.windAnimationLayer(opt);
    _layer.addTo(map);
    return _layer;
}